.trip-card {
     width: 28%;
     width: 32.2222222222%;
     width: 28.8%;
     margin-left: 2.564102564102564%;
     float: left;
     float: left;
	margin-left: 1.66666666667%;
     background: rgba(255,255,255,.9);
     background: #fff;
     padding: 20px;
     border-radius: 10px;
     transition: all ease-in-out .3s;
